.main {
  position: relative;
  padding-bottom: 80px;
  color: #01277C;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -750px;
    width: 100%;
    height: 775px;
    background: radial-gradient(50.00% 18.00% at 50% 33%,rgb(217, 217, 217),rgba(217, 217, 217, 0) 100%);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 4;
  }
  &__desc {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    position: relative;
    max-width: 730px;
    font-weight: 500;
    margin-top: 100px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: -450px;
      transform: rotate(12deg);
      width: 800px;
      height: 350px;
      background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
      pointer-events: none;
    }
  }
  &__text {
    position: relative;
    max-width: 565px;
    display: flex;
    align-items: center;
    gap: 19px;
    margin-top: 50px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -100px;
      transform: translate(0, -50%);
      width: 370px;
      height: 370px;
      background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
      border-radius: 50%;
      pointer-events: none;
    }
    i {
      position: relative;
      z-index: 1;
      width: 16px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    p {
      position: relative;
      z-index: 1;
    }
  }
  &__img2 {
    position: absolute;
    top: 300px;
    right: 0;
    max-width: 1054px;
    pointer-events: none;
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      left: 0;
      bottom: -150px;
      max-width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    z-index: 4;
    top: 100px;
    right: 70px;
    width: 300px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.gost {
  position: relative;
  z-index: 5;
  color: #01277C;
  padding-top: 80px;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    position: relative;
    z-index: 2;
    max-width: 750px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -230px;
      transform: translate(0, -50%);
      width: 430px;
      height: 250px;
      background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
      pointer-events: none;
    }
  }
  &__cards {
    width: calc(100% + 30px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__img {
    position: absolute;
    top: 180px;
    right: -90px;
    width: 317px;
    img {
      width: 100%;
    }
  }
}
.gost-card {
  position: relative;
  width: calc(50% - 30px);
  margin: 0 30px 60px 0;
  &:nth-last-child(-n+2) {
    margin-bottom: 0 !important;
  }
  &__img {
    position: relative;
    img {
      max-width: 100%;
      object-fit: contain;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    max-width: 420px;
  }
  &__title {
    &:hover {
      color: #81b82e;
    }
  }
  &__text {
    margin-top: 10px;
  }
  .circle {
    position: absolute;
    z-index: 1;
    right: -80px;
    bottom: 80px;
    width: 400px;
    height: 400px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 60px;
    transform: translate(0, -50%) rotate(12deg);
    width: 350px;
    height: 700px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
    pointer-events: none;
  }
  .circle {
    position: absolute;
    z-index: 1;
    right: -80px;
    bottom: 80px;
    width: 400px;
    height: 400px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    z-index: 1;
    top: -40px;
    right: -60px;
    width: 370px;
    height: 370px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.13) 84.5%,rgba(255, 255, 255, 0) 100%);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle4 {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 0;
    width: 640px;
    height: 190px;
    transform: translate(0, -50%);
    background: radial-gradient(72.24% 36.88% at 60% 51%,rgb(255, 255, 255),rgba(255, 255, 255, 0.27) 52.279%,rgba(215, 214, 210, 0) 100%);
    pointer-events: none;
    &~img {
      position: relative;
      z-index: 2;
    }
  }
}
.advantages {
  position: relative;
  z-index: 2;
  color: #01277C;
  padding-bottom: 80px;
  &__title {
    text-align: right;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 60px 20px;
    margin-top: 60px;
  }
  &__item {
    &-title {
      position: relative;
      i {
        position: absolute;
        top: 0;
        left: 0;
        width: 48px;
        flex-shrink: 0;
        display: inline-block;
        img {
          max-width: 100%;
        }
      }
      span {
        display: inline-block;
        text-indent: 60px;
      }
    }
    &-text {
      max-width: 400px;
      margin-top: 10px;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 40px 24px;
    margin-top: 60px;
  }
  &__info {
    margin-top: 60px;
  }
}
.advantages2 {
  .advantages__item-title {
    font-size: 60px;
  }
}
.advantages3 {
  padding: 15px 0 40px !important;
}
.news {
  padding: 80px 0;
  color: #01277C;
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 40px;
  }
  &__btn {
    height: 40px;
    border-radius: 100px;
    padding: 5px 10px;
    margin-top: 40px;
    span {
      font-size: 14px;
    }
  }
}
.news-card {
  &__img {
    position: relative;
    display: block;
    padding-bottom: 63%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__desc {
    margin-top: 16px;
  }
  &__date {
    color: #AFAFAF;
    margin-top: 16px;
  }
  &__title {
    &:hover {
      color: #81b82e;
    }
  }
  &__text {
    color: #000206;
    margin-top: 16px;
  }
}
.partners {
  padding: 80px 0;
  color: #01277C;
  &2 {
    padding-bottom: 400px;
  }
  &__subtitle {
    max-width: 730px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 180px / 4));
    gap: 60px;
    margin-top: 60px;
  }
}
.partners3 {
  padding: 40px 0;
  .partners__cards {
    margin-top: 0;
  }
}
.partner-card {
  min-height: 149px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  padding: 5px;
  img {
    max-width: 100%;
  }
}

@media screen and (max-width: 1400px) {
  .advantages {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .partners {
    &2 {
      padding-bottom: 200px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .main {
    &__img2 {
      max-width: 700px;
      img:nth-child(2) {
        bottom: -100px;
      }
    }
    &__img3 {
      top: 50px;
      right: 15px;
      width: 200px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__subtitle {
      max-width: 600px;
      margin-top: 50px;
    }
    &__text {
      max-width: 320px;
      margin-top: 25px;
      i {
        width: 12px;
      }
    }
    &__img2 {
      top: 200px;
    }
  }
  .gost {
    &__subtitle {
      max-width: 450px;
      &:after {
        width: 350px;
        height: 200px;
      }
    }
    &__img {
      right: -40px;
      width: 200px;
    }
  }
  .gost-card {
    .circle2 {
      width: 250px;
      height: 600px;
    }
  }
  .advantages {
    &__item {
      &-title {
        i {
          width: 36px;
        }
        span {
          text-indent: 46px;
        }
      }
    }
  }
  .advantages2 {
    .advantages__item-title {
      font-size: 40px;
    }
  }
  .partners {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 120px / 4));
      gap: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-bottom: 50px;
    &:after {
      display: none;
    }
    &__subtitle {
      max-width: 360px;
      margin-top: 30px;
      &:before {
        right: -250px;
        width: 400px;
        height: 150px;
      }
    }
    &__text {
      max-width: 250px;
      gap: 16px;
      margin-top: 15px;
      &:after {
        display: none;
      }
      i {
        width: 7px;
      }
    }
    &__img2 {
      top: 100px;
      right: 100px;
      max-width: 400px;
      img:nth-child(2) {
        display: none;
      }
    }
    &__img3 {
      width: 100px;
    }
  }
  .gost {
    padding-top: 50px;
    &__subtitle {
      max-width: 335px;
      &:after {
        display: none;
      }
    }
  }
  .gost-card {
    .circle,
    .circle2,
    .circle3,
    .circle4 {
      display: none;
    }
  }
  .advantages {
    padding-bottom: 50px;
    &__items {
      gap: 20px;
      margin-top: 30px;
    }
    &__item {
      &-title {
        i {
          width: 30px;
        }
        span {
          text-indent: 40px;
        }
      }
    }
    &__cards {
      margin-top: 30px;
    }
    &__info {
      margin-top: 30px;
    }
  }
  .advantages2 {
    .advantages__item-title {
      font-size: 30px;
    }
  }
  .advantages3 {
    padding-bottom: 30px !important;
  }
  .news {
    padding: 50px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
      margin-top: 30px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .partners {
    padding: 50px 0;
    &2 {
      padding-bottom: 100px;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__img2 {
      right: 0;
    }
  }
  .partners {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 650px) {
  .main {
    &__img2 {
      max-width: 300px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 30px;
    &__subtitle {
      max-width: 100%;
    }
    &__img2 {
      position: static;
      margin: 15px auto 0;
    }
    &__img3 {
      display: none;
    }
  }
  .gost {
    padding-top: 30px;
    &__cards {
      width: calc(100% + 15px);
    }
  }
  .gost-card {
    margin: 0 15px 45px 0;
  }
  .advantages {
    padding: 30px 0;
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &-title {
        br {
          display: none;
        }
      }
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 20px;
    }
  }
  .news {
    padding: 30px 0;
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .news-card {
    &__date {
      margin-top: 10px;
    }
    &__text {
      margin-top: 10px;
    }
  }
  .partners {
    padding: 30px 0;
    &2 {
      padding-bottom: 60px;
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .advantages3 {
    padding-top: 15px;
  }
}