.header {
  position: relative;
  z-index: 10;
  padding: 68px 0 48px;
  color: #01277C;
  .container {
    max-width: 1560px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 45px;
  }
  &__account {
    display: flex;
    align-items: center;
    gap: 16px;
    &:hover {
      color: #004bdb;
    }
    i {
      width: 13px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 110%;
    }
  }
  .menu-fixed {
    position: fixed;
    z-index: 8;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #FDF9E2;
    padding: 150px 15px 50px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    .menu {
      flex-direction: column;
      gap: 45px;
    }
  }
}
.burger {
  position: relative;
  width: 20px;
  height: 15px;
  &-wrap {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    gap: 16px;
    cursor: pointer;
    &:hover {
      .burger-text {
        color: #004bdb;
      }
    }
  }
  &-text {
    font-size: 20px;
    line-height: 105%;
    transition: all 0.3s;
  }
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #FF0000;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 6px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 6px;
      transform: rotate(45deg);
    }
  }
}
.menu {
  display: flex;
  align-items: center;
  gap: 45px;
  &-item {
    a {
      font-size: 20px;
      line-height: 110%;
      &:hover {
        color: #004bdb;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__inner {
      gap: 30px;
    }
  }
  .menu {
    gap: 30px;
  }
}

@media screen and (max-width: 1200px) {
  .header {
    padding-top: 48px;
    &__inner {
      gap: 16px;
    }
    .menu-fixed {
      .menu-item {
        a {
          font-size: 18px;
        }
      }
    }
  }
  .menu {
    gap: 16px;
    &-item {
      a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 30px 0;
    .menu-fixed {
      padding-top: 100px;
      .menu {
        display: flex;
        gap: 24px;
      }
      .menu-item {
        a {
          font-size: 16px;
        }
      }
    }
  }
  .menu {
    display: none;
  }
}