*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  box-sizing: border-box;
}

body,
div,
p,
h1,
h2,
h3,
h4,
h5,
ul,
li,
a,
form,
label {
  margin: 0;
  padding: 0;
  border: 0;
  text-decoration: none;
  line-height: 1;
  box-sizing: border-box;
}

button,
input,
textarea {
  outline: none !important;
}

button {
  border: none;
  cursor: pointer;
  transition: all 0.5s;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

input[type=text]:focus,
input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus,
input:active {
  outline: none !important;
}

html {
  height: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: 'SuisseIntl', sans-serif;
  font-weight: 400;
  height: 100%;
  background: #FDF9E2;
  color: #000206;
  overflow-x: hidden;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1000px;
    background: rgba(160, 125, 0, 0.03);
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 580px;
    left: 0;
    width: 100%;
    height: 630px;
    background: radial-gradient(141.90% 42.20% at 50% 50%,rgba(255, 255, 255, 0.85),rgba(255, 255, 255, 0.23) 52.279%,rgba(215, 214, 210, 0) 100%);
    pointer-events: none;
  }
}
.fixed-body {
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
img {
  margin: 0;
  padding: 0;
  height: auto;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

a {
  color: inherit;
  text-decoration: none;
  transition: 0.3s;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1335px;
}

@media screen and (max-width: 992px) {
  .container {
    padding: 0 15px;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding: 0 10px;
  }
}