.footer {
  position: relative;
  font-family: 'Inter';
  color: #01277C;
  &__gradient {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 430px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgba(249, 247, 247, 0.85),rgba(255, 252, 252, 0.68) 75.115%,rgba(255, 255, 255, 0) 100%);
    pointer-events: none;
  }
  &__gradient2 {
    position: absolute;
    z-index: 1;
    left: -40%;
    bottom: -40px;
    width: 100%;
    height: 280px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(217, 217, 217),rgba(217, 217, 217, 0) 100%);
    pointer-events: none;
  }
  &__gradient3 {
    position: absolute;
    z-index: 1;
    right: -40%;
    bottom: -40px;
    width: 100%;
    height: 280px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(217, 217, 217),rgba(217, 217, 217, 0) 100%);
    pointer-events: none;
  }
  &__gradient4 {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: 60px;
    transform: translate(-50%, 0);
    width: 100%;
    height: 370px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(217, 217, 217),rgba(217, 217, 217, 0) 100%);
    pointer-events: none;
  }
  &__gradient5 {
    position: absolute;
    z-index: 1;
    top: -100px;
    right: -100px;
    width: 1200px;
    height: 450px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(243, 243, 235),rgba(255, 252, 252, 0.16) 73.272%,rgba(255, 255, 255, 0) 100%);
    pointer-events: none;
  }
  &__gradient6 {
    position: absolute;
    z-index: 1;
    top: -120px;
    right: -120px;
    width: 1200px;
    height: 450px;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(243, 243, 235),rgba(255, 252, 252, 0.16) 73.272%,rgba(255, 255, 255, 0) 100%);
    pointer-events: none;
  }
  &__img {
    position: absolute;
    z-index: -1;
    left: 50%;
    bottom: 100px;
    transform: translate(-50%, 0);
    width: 100%;
    img {
      width: 100%;
    }
  }
}
.footer-top {
  position: relative;
  z-index: 2;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 60px;
  }
  &__logo {
    width: 260px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__items {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__item {
    max-width: 390px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 177%;
    font-weight: 600;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    li a {
      font-size: 18px;
      line-height: 177%;
      &:hover {
        color: #004bdb;
      }
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
  }
  &__subitem {
    font-size: 18px;
    line-height: 177%;
    &:hover {
      color: #004bdb;
    }
    strong {
      font-weight: 600;
    }
  }
  .route {
    display: block;
    text-decoration: underline;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: #004bdb;
    }
  }
}
.footer-bottom {
  position: relative;
  z-index: 2;
  padding: 25px 0;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 10px 20px;
    p {
      font-size: 12px;
      line-height: 170%;
    }
    a {
      text-decoration: underline;
      &:hover {
        color: #004bdb;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__img {
      min-width: 1500px;
    }
    &__gradient4 {
      bottom: 40px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer-top {
    &__inner {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__img {
      display: none;
    }
    &__gradient,
    &__gradient2,
    &__gradient3,
    &__gradient4 {
      display: none;
    }
  }
  .footer-top {
    &__logo {
      width: 160px;
    }
    &__list {
      li a {
        font-size: 16px;
      }
    }
    &__subitem {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__items {
      flex-direction: column;
    }
  }
}

