.info-main {
  position: relative;
  z-index: 2;
  padding-bottom: 15px;
  color: #01277C;
  &__inner {
    position: relative;
    z-index: 4;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 10px;
    margin-top: 40px;
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
    &:after {
      content: '';
      position: absolute;
      left: -355px;
      bottom: -60px;
      width: 1085px;
      height: 288px;
      background: radial-gradient(48.01% 48.00% at 50% 52%,rgb(255, 248, 241),rgba(255, 248, 241, 0.71) 32.639%,rgba(255, 248, 241, 0.54) 63.5%,rgba(255, 248, 241, 0) 100%);
      pointer-events: none;
    }
  }
  &__img {
    width: 50%;
    img {
      border-radius: 20px;
    }
    img:nth-child(1) {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      right: -170px;
      bottom: -240px;
      max-width: 1000px;
      pointer-events: none;
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    font-weight: 500;
    margin-bottom: 40px;
  }
  &__text {
    position: relative;
    z-index: 2;
    p {
      line-height: 165%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 5;
    right: 0;
    bottom: 0;
    width: 300px;
    img {
      width: 100%;
    }
  }
}
.advantage-card {
  &__img {
    position: relative;
    padding-bottom: 64%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  &__text {
    font-size: 20px;
    line-height: 165%;
  }
  &__list {
    position: relative;
    li {
      font-size: 15px;
      line-height: 166%;
    }
  }
  .number-list {
    padding-left: 20px;
    li {
      list-style: decimal;
    }
  }
  .circle-list {
    li {
      position: relative;
      padding-left: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 9px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #01277C;
        border-radius: 50%;
      }
    }

  }
}
.connection {
  position: relative;
  z-index: 5;
  padding: 40px 0 100px;
  color: #01277C;
  .section-top {
    .connection__subtitle {
      margin-top: 0;
    }
  }
  &__subtitle {
    margin-top: 30px;
  }
  &__form {
    display: flex;
    align-items: center;
    gap: 24px;
    font-family: 'Inter';
    margin-top: 30px;
    &-left {
      width: calc(100% - 366px);
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 48px / 2));
      gap: 24px 48px;
    }
    &-right {
      width: 332px;
    }
    &-group {
      label {
        display: block;
        font-size: 14px;
        line-height: 130%;
        color: #5E6678;
        margin-bottom: 12px;
      }
      input {
        width: 100%;
        height: 56px;
        background: #F1F5F9;
        border: 1px solid #D7D6D2;
        border-radius: 8px;
        padding: 0 16px;
        font-size: 16px;
        line-height: 120%;
        color: #20242D;
        &::placeholder {
          color: rgba(32, 36, 45, 0.5);
        }
      }
      textarea {
        width: 100%;
        background: #F1F5F9;
        border: 1px solid #D7D6D2;
        border-radius: 8px;
        padding: 16px;
        font-size: 16px;
        line-height: 120%;
        color: #20242D;
        resize: none;
        &::placeholder {
          color: rgba(32, 36, 45, 0.5);
        }
      }
    }
    .form-group__big {
      grid-column: 2;
      grid-row: 1/3;
      display: flex;
      flex-direction: column;
      textarea {
        flex-grow: 1;
      }
    }
    &-check {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
      }
      input:checked ~ label {
        &:after {
          background-image: url("../img/check.svg");
        }
      }
      label {
        position: relative;
        display: block;
        font-size: 13px;
        line-height: 140%;
        color: #4E5154;
        padding-left: 30px;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 20px;
          height: 20px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          background-color: #fff;
          border: 1px solid #DADEDF;
          border-radius: 3px;
        }
        a {
          position: relative;
          z-index: 3;
          text-decoration: underline;
          &:hover {
            color: #004bdb;
          }
        }
      }
    }
    &-btn {
      width: 100%;
      height: 60px;
      border-radius: 100px;
      margin-top: 30px;
      span {
        font-size: 14px;
      }
    }
  }
}
.catalog {
  padding: 40px 0;
  color: #01277C;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 170px / 3));
    gap: 85px;
    margin-top: 40px;
  }
  &__btn {
    height: 40px;
    border-radius: 100px;
    margin-top: 60px;
    span {
      font-size: 14px;
    }
  }
}
.catalog-card {
  &__img {
    position: relative;
    display: block;
    padding-bottom: 110%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__desc {
    margin-top: 16px;
  }
  &__text {
    margin-top: 10px;
  }
}

@media screen and (max-width: 1200px) {
  .info-main {
    &__title {
      margin-bottom: 20px;
    }
    &__img2 {
      width: 200px;
    }
  }
  .advantage-card {
    &__text {
      font-size: 16px;
    }
  }
  .connection {
    &__form {
      gap: 24px;
      &-left {
        width: calc(100% - 324px);
        grid-template-columns: repeat(2, calc(50% - 24px / 2));
        gap: 24px;
      }
      &-right {
        width: 300px;
      }
      &-btn {
        margin-top: 24px;
      }
    }
  }
  .catalog {
    &__cards {
      grid-template-columns: repeat(3,calc(100% / 3 - 80px / 3));
      gap: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .info-main {
    &__desc {
      &:after {
        display: none;
      }
    }
    &__img {
      img:nth-child(2) {
        max-width: 800px;
      }
    }
  }
  .advantage-card {
    &__desc {
      gap: 10px;
    }
  }
  .connection {
    padding: 30px 0 60px;
    &__form {
      flex-direction: column;
      gap: 20px;
      &-left {
        width: 100%;
        grid-template-columns: repeat(2, calc(50% - 20px / 2));
        gap: 20px;
      }
      &-right {
        width: 100%;
      }
    }
  }
  .catalog {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
      margin-top: 30px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .catalog-card {
    &__title {
      font-size: 18px;
    }
    &__text {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 768px) {
  .info-main {
    &__wrap {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      img:nth-child(2) {
        display: none;
      }
    }
    &__img2 {
      display: none;
    }
  }
  .connection {
    &__form {
      &-left {
        grid-template-columns: 100%;
      }
      .form-group__big {
        grid-column: unset;
        grid-row: unset;
        textarea {
          height: 164px;
        }
      }
    }
  }
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
}
