.wrapper {
  overflow: hidden;
}
.text128 {
  font-size: 128px;
  line-height: 100%;
  font-weight: 400;
  letter-spacing: -12.8px;
  color: #81B82E;
}
.text64 {
  font-size: 64px;
  line-height: 100%;
  letter-spacing: -3.2px;
}
.text40 {
  font-size: 40px;
  line-height: 100%;
  letter-spacing: -2px;
}
.text32 {
  font-size: 32px;
  line-height: 100%;
  letter-spacing: -1.6px;
}
.text15 {
  font-size: 15px;
  line-height: 150%;
}
.section-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  gap: 10px;
}
.btn-gray {
  background: #EEEFE6;
  border: 1px solid #80B82C;
  &:hover {
    background: #80B82C;
    span {
      color: #fff;
    }
  }
  span {
    font-family: 'Inter';
    line-height: 120%;
    font-weight: 600;
    color: #80B82C;
  }
}
.btn-green {
  background: #81B82E;
  border: 1px solid transparent;
  &:hover {
    background: #EEEFE6;
    border-color: #81B82E;
  }
  span {
    font-family: 'Inter';
    line-height: 120%;
    color: #fff;
  }
}
.page-top {
  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -2.4px;
  }
  &__logo {
    max-width: 768px;
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 1650px;
    width: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .text128 {
    font-size: 80px;
    letter-spacing: -8px;
  }
  .text64 {
    font-size: 48px;
    letter-spacing: -2.4px;
  }
  .text40 {
    font-size: 24px;
    letter-spacing: -1.2px;
  }
  .text32 {
    font-size: 20px;
    letter-spacing: -1px;
  }
  .page-top {
    &__title {
      font-size: 32px;
      letter-spacing: -1.6px;
    }
    &__logo {
      max-width: 500px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .text128 {
    font-size: 80px;
    letter-spacing: -8px;
  }
  .text40 {
    font-size: 24px;
    letter-spacing: -1.2px;
  }
}

@media screen and (max-width: 992px) {
  .text128 {
    font-size: 60px;
    letter-spacing: -6px;
  }
  .text64 {
    font-size: 30px;
    letter-spacing: -1.5px;
  }
  .text40 {
    font-size: 18px;
    letter-spacing: -0.9px;
  }
  .text32 {
    font-size: 16px;
    letter-spacing: -0.8px;
  }
  .page-top {
    &__title {
      font-size: 24px;
      letter-spacing: -1.2px;
    }
    &__logo {
      max-width: 400px;
    }
  }
}

@media screen and (max-width: 576px) {
  .text128 {
    font-size: 40px;
    letter-spacing: -4px;
  }
  .text15 {
    font-size: 14px;
  }
  .section-top {
    flex-direction: column;
    align-items: start;
  }
  .page-top {
    &__img {
      min-width: 450px;
    }
  }
}