.contacts {
  position: relative;
  z-index: 5;
  padding-bottom: 130px;
  color: #01277C;
  &__inner {
    position: relative;
    z-index: 4;
  }
  &__title {
    margin-top: 80px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 60px 20px;
    margin-top: 20px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &--big {
      grid-column: 2;
      grid-row: 1/5;
    }
    &-text {
      display: flex;
      flex-direction: column;
      font-size: 20px;
      line-height: 165%;
      color: #000206;
      margin-top: 12px;
      a {
        line-height: 165%;
      }
      a:hover{
        color: #01277C;
      }
    }
    .map-wrap {
      flex-grow: 1;
      margin-top: 24px;
      iframe {
        width: 100%;
        height: 100%;
        border: none;
        box-shadow: 8px 4px 4px 0px rgba(0, 0, 0, 0.25);
        border-radius: 20px;

      }
    }
    .map {
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__item {
      &-text {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding-bottom: 100px;
    &__title {
      margin-top: 60px;
    }
    &__items {
      gap: 40px 20px;
    }
    &__item {
      &--big {
        grid-row: 1/4;
      }
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    padding-bottom: 60px;
    &__title {
      margin-top: 40px;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__item {
      &--big {
        grid-column: unset;
        grid-row: unset;
      }
      .map-wrap {
        iframe {
          min-height: 300px;
        }
      }
    }
  }
}